'use server';

import { summarizeTerrainAnalysis } from '@/ai/flows/summarize-terrain-analysis';
import { optimizeZoneDistribution } from '@/ai/flows/optimize-zone-distribution';
import { simulateTrafficAndPollution } from '@/ai/flows/simulate-traffic-and-pollution';
import { generateCityPlan } from '@/ai/flows/generate-city-plan';
import type { CityParams, FullReport, CityPlan, MapData } from '@/lib/types';

export async function analyzeImage(imageData: string): Promise<string> {
  if (!imageData) {
    throw new Error('Image data is required for analysis.');
  }

  // The provided Genkit flow requires a `terrainAnalysis` string.
  // Since we cannot generate it from the image alone with the given flows,
  // we will provide a mock, descriptive analysis to satisfy the flow's input schema.
  const mockTerrainAnalysis =
    'The satellite image displays a varied landscape. The area is predominantly flat with a gentle slope towards the south. A river meanders from the northwest to the southeast, suggesting ample water supply but also flood-prone zones. Patches of dense green indicate fertile soil, suitable for parks or agriculture. A rocky outcrop is visible in the northeast quadrant, which might be challenging for construction but could be a source of materials. There is no existing infrastructure visible.';

  try {
    const result = await summarizeTerrainAnalysis({
      satelliteImageDataUri: imageData,
      terrainAnalysis: mockTerrainAnalysis,
    });
    return result.summary;
  } catch (error) {
    console.error('Error in summarizeTerrainAnalysis flow:', error);
    throw new Error('Failed to analyze terrain from image.');
  }
}

export async function getOptimizedZones(
  terrainSummary: string,
  population: number,
  budget: number
) {
  try {
    const result = await optimizeZoneDistribution({
      terrainAnalysis: terrainSummary,
      population,
      budget,
    });
    return result;
  } catch (error) {
    console.error('Error in optimizeZoneDistribution flow:', error);
    throw new Error('Failed to optimize zone distribution.');
  }
}

export async function generatePlanAndSimulate(
  terrainSummary: string,
  cityParams: CityParams
): Promise<FullReport> {

  try {
    const planResult = await generateCityPlan({
      terrainAnalysis: terrainSummary,
      population: cityParams.population,
      budget: cityParams.budget,
      zoneDistribution: JSON.stringify(cityParams.zoneDistribution),
      specialRequirements: JSON.stringify(cityParams.specialRequirements),
    });

    if (!planResult || !planResult.mapData || !planResult.report) {
      console.error('Invalid planResult received from AI:', planResult);
      throw new Error("AI failed to return a valid city plan. The response was incomplete or malformed. Please try again.");
    }

    const cityPlan: CityPlan = planResult.report;
    const mapData: MapData = planResult.mapData;

    const simulation = await simulateTrafficAndPollution({
      cityPlan: JSON.stringify({ ...cityPlan, parameters: cityParams }),
    });

    return { cityPlan, simulation, cityParams, mapData };
  } catch (error) {
     console.error('Error in plan generation or simulation flow:', error);
     if (error instanceof Error) {
        throw error; // Re-throw the specific error
     }
    throw new Error('An unexpected error occurred during plan generation.');
  }
}
