'use client';

import type { FC } from 'react';
import React, { useState, useTransition } from 'react';
import { Header } from '@/components/layout/header';
import ImageUpload from '@/components/steps/image-upload';
import ParametersForm from '@/components/steps/parameters-form';
import CityReport from '@/components/steps/city-report';
import { Loader2, AlertTriangle } from 'lucide-react';
import type { CityParams, FullReport } from '@/lib/types';
import { generatePlanAndSimulate } from '@/app/actions';
import { useToast } from "@/hooks/use-toast"
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';

const ErrorDisplay: React.FC<{ error: string; onRetry: () => void }> = ({ error, onRetry }) => {
  if (!error) return null;

  return (
    <Card className="max-w-4xl mx-auto shadow-lg bg-destructive/10 border-destructive">
      <CardHeader className="text-center">
        <div className="flex justify-center items-center mb-2">
           <div className="p-3 bg-destructive/10 rounded-full">
             <AlertTriangle className="w-8 h-8 text-destructive" />
           </div>
        </div>
        <CardTitle className="text-2xl text-destructive">Generation Failed</CardTitle>
      </CardHeader>
      <CardContent className="space-y-4 text-center">
        <div className="bg-background/50 p-4 rounded-md text-sm text-destructive-foreground">
          <p><strong>Error Details:</strong> {error}</p>
        </div>
        <div className="text-sm text-muted-foreground">
            <h4 className="font-semibold mb-2">Debugging Steps:</h4>
            <ol className="list-decimal list-inside text-left mx-auto max-w-md">
              <li>Ensure all parameters are filled correctly.</li>
              <li>Check your internet connection.</li>
              <li>Try with a smaller population or budget.</li>
              <li>Check the browser console (F12) for more details.</li>
            </ol>
        </div>
        <Button onClick={onRetry} variant="destructive">
          🔄 Try Again
        </Button>
      </CardContent>
    </Card>
  );
};


const Page: FC = () => {
  const [step, setStep] = useState(1);
  const [isPending, startTransition] = useTransition();
  const [error, setError] = useState<string | null>(null);
  const { toast } = useToast();

  const [imageData, setImageData] = useState<string | null>(null);
  const [terrainSummary, setTerrainSummary] = useState<string | null>(null);
  const [cityParams, setCityParams] = useState<CityParams | null>(null);
  const [fullReport, setFullReport] = useState<FullReport | null>(null);

  const handleImageProcessed = (imgData: string, summary: string) => {
    setImageData(imgData);
    setTerrainSummary(summary);
    setStep(2);
    setError(null);
  };
  
  const handleRetry = () => {
    setError(null);
    if (cityParams && terrainSummary) {
       handleParamsSubmit(cityParams);
    } else {
      handleStartOver();
    }
  };

  const handleParamsSubmit = (params: CityParams) => {
    setCityParams(params);
    if (!terrainSummary) {
      setError('Terrain analysis is missing.');
      toast({
        variant: "destructive",
        title: "Error",
        description: "Terrain analysis is missing. Please re-upload the image.",
      });
      return;
    }

    startTransition(async () => {
      try {
        setError(null);
        setFullReport(null);
        const result = await generatePlanAndSimulate(terrainSummary, params);
        if(!result.mapData || !result.cityPlan){
           throw new Error("AI failed to return valid map data or a city plan. The response might be incomplete.");
        }
        setFullReport(result);
        setStep(3);
      } catch (e) {
        const error = e instanceof Error ? e : new Error('An unknown error occurred');
        console.error('Plan generation failed:', error);
        setError(error.message);
        setStep(2); // Stay on params form to allow retry
      }
    });
  };

  const handleStartOver = () => {
    setStep(1);
    setImageData(null);
    setTerrainSummary(null);
    setFullReport(null);
    setCityParams(null);
    setError(null);
  };

  const renderContent = () => {
    if (error) {
      return <ErrorDisplay error={error} onRetry={handleRetry} />;
    }

    switch (step) {
      case 1:
        return <ImageUpload onImageProcessed={handleImageProcessed} />;
      case 2:
        return (
          terrainSummary && (
            <ParametersForm
              terrainSummary={terrainSummary}
              onParamsSubmit={handleParamsSubmit}
              initialValues={cityParams}
            />
          )
        );
      case 3:
        return fullReport && <CityReport report={fullReport} onStartOver={handleStartOver} imageData={imageData} />;
      default:
        return <p>An unexpected error occurred. Please refresh the page.</p>;
    }
  };

  return (
    <div className="flex flex-col min-h-screen bg-background font-body text-foreground">
      <Header />
      <main className="flex-grow container mx-auto px-4 py-8 md:py-12">
        <div className="relative">
          {isPending && (
            <div className="absolute inset-0 bg-background/80 flex flex-col items-center justify-center z-50 rounded-lg">
              <Loader2 className="w-16 h-16 animate-spin text-primary" />
              <p className="mt-4 text-lg font-medium">
                AI is generating your city... this may take a moment.
              </p>
            </div>
          )}
          <div className={isPending ? 'opacity-50 blur-sm pointer-events-none' : ''}>
            {renderContent()}
          </div>
        </div>
      </main>
      <footer className="py-4 text-center text-sm text-muted-foreground">
        <p>Powered by CityWise AI</p>
      </footer>
    </div>
  );
};

export default Page;
